import React, { useState } from 'react';
import { CategoryBlock } from '../types';

interface CategoryBlockProps {
  category: CategoryBlock;
  children?: React.ReactNode; //    
  isDragging?: boolean;
  isSelected?: boolean;
  isConnecting?: boolean;
  isDisconnectMode?: boolean;
  connectingFromId?: string | null;
  onUpdate: (category: CategoryBlock) => void;
  onDelete: (categoryId: string) => void;
  onToggleExpanded: (categoryId: string) => void;
  onDragStart?: (e: React.DragEvent) => void;
  onDragEnd?: (e: React.DragEvent) => void;
  onDrop?: (e: React.DragEvent) => void;
  onDragOver?: (e: React.DragEvent) => void;
  onClick?: (categoryId: string, isShiftClick?: boolean) => void;
  onStartConnection?: (categoryId: string) => void;
  onConnectItems?: (fromId: string, toId: string) => void;
  onRemoveConnection?: (fromId: string, toId: string) => void;
  onPositionChange?: (categoryId: string, position: { x: number; y: number }) => void;
  onSizeChange?: (id: string, size: { width: number; height: number }) => void;
  canvasScale?: number;
  canvasOffset?: { x: number; y: number };
}

const CategoryBlockComponent: React.FC<CategoryBlockProps> = ({
  category,
  children,
  isDragging = false,
  isSelected = false,
  isConnecting = false,
  isDisconnectMode = false,
  connectingFromId = null,
  onUpdate,
  onDelete,
  onToggleExpanded,
  onDragStart,
  onDragEnd,
  onDrop,
  onDragOver,
  onClick,
  onStartConnection,
  onConnectItems,
  onRemoveConnection,
  onPositionChange,
  onSizeChange,
  canvasScale = 1,
  canvasOffset = { x: 0, y: 0 }
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editTitle, setEditTitle] = useState(category.title);
  const [isHovered, setIsHovered] = useState(false);

  // MemoBlock    
  const [isDraggingPosition, setIsDraggingPosition] = useState(false);
  const [isConnectionDragging, setIsConnectionDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const [dragMoved, setDragMoved] = useState(false);
  const [isHtml5Dragging, setIsHtml5Dragging] = useState(false);
  const titleRef = React.useRef<HTMLInputElement>(null);
  const categoryRef = React.useRef<HTMLDivElement>(null);

  const handleTitleClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    setIsEditing(true);
    setEditTitle(category.title);
    setTimeout(() => titleRef.current?.focus(), 0);
  };

  const handleTitleSave = () => {
    setIsEditing(false);
    if (editTitle.trim() !== category.title) {
      onUpdate({
        ...category,
        title: editTitle.trim() || 'Untitled Category'
      });
    }
  };

  const handleTitleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleTitleSave();
    } else if (e.key === 'Escape') {
      setIsEditing(false);
      setEditTitle(category.title);
    }
  };

  const handleExpandToggle = (e: React.MouseEvent) => {
    e.stopPropagation();
    onToggleExpanded(category.id);
  };

  const handleClick = (e: React.MouseEvent) => {
    // MemoBlock  dragMoved  (isConnectionDragging )
    if (!dragMoved && !isEditing) {
      onClick?.(category.id, e.shiftKey);
    }
  };

  const handleDelete = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (window.confirm(` "${category.title}" ?    .`)) {
      onDelete(category.id);
    }
  };

  // MemoBlock   
  const handleMouseDown = (e: React.MouseEvent) => {
    if (e.button === 0 && !isConnecting && !isHtml5Dragging) {
      setIsDraggingPosition(true);
      setDragMoved(false);
      // MemoBlock   canvasScale canvasOffset 
      setDragStart({
        x: e.clientX - (category.position.x * canvasScale + canvasOffset.x),
        y: e.clientY - (category.position.y * canvasScale + canvasOffset.y)
      });
      e.preventDefault();
    }
  };

  const handleConnectionPointMouseDown = (e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();

    if (!isConnecting) {
      setIsConnectionDragging(true);
      onStartConnection?.(category.id);
    }
  };

  const handleConnectionPointMouseUp = (e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();

    if (isConnecting && connectingFromId && connectingFromId !== category.id) {
      onConnectItems?.(connectingFromId, category.id);
    }
    setIsConnectionDragging(false);
  };

  const handleMouseMove = React.useCallback((e: MouseEvent) => {
    if (isDraggingPosition && onPositionChange) {
      if (!dragMoved) {
        setDragMoved(true);
      }
      // MemoBlock   canvasScale canvasOffset 
      const newPosition = {
        x: (e.clientX - dragStart.x - canvasOffset.x) / canvasScale,
        y: (e.clientY - dragStart.y - canvasOffset.y) / canvasScale
      };
      onPositionChange(category.id, newPosition);
    }
  }, [isDraggingPosition, onPositionChange, dragMoved, dragStart, canvasOffset, canvasScale, category.id]);

  const handleMouseUp = React.useCallback(() => {
    setIsDraggingPosition(false);
  }, []);

  React.useEffect(() => {
    if (isDraggingPosition) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      return () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };
    }
  }, [isDraggingPosition, handleMouseMove, handleMouseUp]);

  // ResizeObserver    (  )
  React.useEffect(() => {
    if (!categoryRef.current || !onSizeChange || isDraggingPosition) return;

    const updateSize = () => {
      if (categoryRef.current && !isDraggingPosition) {
        const rect = categoryRef.current.getBoundingClientRect();
        // scale     
        const newSize = {
          width: rect.width / canvasScale,
          height: rect.height / canvasScale
        };
        if (!category.size || category.size.width !== newSize.width || category.size.height !== newSize.height) {
          onSizeChange(category.id, newSize);
        }
      }
    };

    updateSize(); //   

    const resizeObserver = new ResizeObserver(updateSize);
    resizeObserver.observe(categoryRef.current);

    return () => {
      resizeObserver.disconnect();
    };
  }, [category.id, category.title, category.tags, category.children, onSizeChange, canvasScale, isDraggingPosition]);

  //     (    )
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    onDragOver?.(e);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    onDrop?.(e);
  };

  // MemoBlock    -  
  const categoryStyle: React.CSSProperties = {
    width: '100%', //   
    minWidth: '200px',
    minHeight: '80px',
    backgroundColor: isSelected ? '#7c3aed' : '#8b5cf6',
    border: isSelected ? '2px solid #6d28d9' : '1px solid #7c3aed',
    borderRadius: '8px',
    padding: '16px',
    boxShadow: isDragging ? '0 8px 16px rgba(0,0,0,0.3)' : '0 2px 8px rgba(0,0,0,0.1)',
    cursor: isDraggingPosition ? 'grabbing' : 'grab',
    opacity: isDragging ? 0.7 : 1,
    transition: isDraggingPosition ? 'none' : 'all 0.2s ease',
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
    userSelect: 'none',
    zIndex: 1,
    boxSizing: 'border-box'
  };

  const headerStyle: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    marginBottom: category.isExpanded && children ? '12px' : '0',
    minHeight: '24px',
    width: '100%', //   
    overflow: 'hidden' //   
  };

  const expandButtonStyle: React.CSSProperties = {
    background: 'none',
    border: 'none',
    cursor: 'pointer',
    padding: '4px',
    marginRight: '8px',
    fontSize: '14px',
    color: 'white',
    display: children ? 'flex' : 'none',
    alignItems: 'center',
    justifyContent: 'center'
  };


  const titleStyle: React.CSSProperties = {
    flex: 1,
    fontSize: '16px',
    fontWeight: 600,
    color: 'white',
    backgroundColor: 'transparent',
    border: isEditing ? '1px solid #1976d2' : 'none',
    borderRadius: '4px',
    padding: '4px 8px',
    outline: 'none',
    minWidth: 0, // flex    
    maxWidth: '100%', //    
    boxSizing: 'border-box' //     
  };

  const controlsStyle: React.CSSProperties = {
    display: isHovered ? 'flex' : 'none',
    alignItems: 'center',
    marginLeft: '8px'
  };

  const deleteButtonStyle: React.CSSProperties = {
    background: 'none',
    border: 'none',
    cursor: 'pointer',
    padding: '4px 8px',
    fontSize: '14px',
    color: '#f44336',
    borderRadius: '4px'
  };

  const childrenContainerStyle: React.CSSProperties = {
    marginLeft: '16px',
    paddingLeft: '12px',
    borderLeft: '2px solid #e0e0e0',
    display: category.isExpanded ? 'block' : 'none'
  };

  const tagsStyle: React.CSSProperties = {
    display: 'flex',
    flexWrap: 'wrap',
    gap: '6px',
    marginTop: category.tags.length > 0 ? '8px' : '0'
  };

  const tagStyle: React.CSSProperties = {
    backgroundColor: '#e3f2fd',
    color: '#1976d2',
    padding: '4px 8px',
    borderRadius: '12px',
    fontSize: '12px',
    fontWeight: 500
  };

  return (
    <div style={{
      position: 'absolute',
      left: category.position.x,
      top: category.position.y,
      width: category.size?.width ? `${category.size.width}px` : 'auto',
      height: 'auto'
    }}>
      {/*    */}
      <div
        ref={categoryRef}
        data-category-block="true"
        draggable
        onDragStart={(e) => {
          setIsHtml5Dragging(true);
          e.dataTransfer.setData('text/plain', JSON.stringify({
            type: 'category',
            id: category.id
          }));
        }}
        onDragEnd={() => {
          setIsHtml5Dragging(false);
        }}
        style={categoryStyle}
        onDragOver={handleDragOver}
        onDrop={handleDrop}
        onClick={handleClick}
        onMouseDown={handleMouseDown}
        onMouseUp={(e) => {
          //        
          if (isConnecting && connectingFromId && connectingFromId !== category.id) {
            e.stopPropagation();
            onConnectItems?.(connectingFromId, category.id);
          }
        }}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
      >
      <div style={headerStyle}>
        <button
          style={expandButtonStyle}
          onClick={handleExpandToggle}
          title={category.isExpanded ? '' : ''}
        >
          {category.isExpanded ? '' : ''}
        </button>


        {isEditing ? (
          <input
            ref={titleRef}
            style={titleStyle}
            value={editTitle}
            onChange={(e) => setEditTitle(e.target.value)}
            onBlur={handleTitleSave}
            onKeyDown={handleTitleKeyDown}
            placeholder=" ..."
          />
        ) : (
          <div
            style={titleStyle}
            onClick={handleTitleClick}
            title=" "
          >
            {category.title}
          </div>
        )}

        <div style={controlsStyle}>
          <button
            style={deleteButtonStyle}
            onClick={handleDelete}
            title=" "
          >
            
          </button>
        </div>
      </div>

      {/*   */}
      {category.tags.length > 0 && (
        <div style={tagsStyle}>
          {category.tags.map((tag, index) => (
            <span key={index} style={tagStyle}>
              #{tag}
            </span>
          ))}
        </div>
      )}

      {/*   */}
      {category.isExpanded && children && (
        <div style={childrenContainerStyle}>
          {children}
        </div>
      )}

      {/* MemoBlock  4  */}
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          top: -8,
          left: '50%',
          transform: 'translateX(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          bottom: -8,
          left: '50%',
          transform: 'translateX(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          left: -8,
          top: '50%',
          transform: 'translateY(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          right: -8,
          top: '50%',
          transform: 'translateY(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>

      {/*    */}
      {category.isExpanded && (
        <div
          style={{
            minHeight: '20px',
            textAlign: 'center',
            color: 'white',
            fontSize: '12px',
            padding: '8px',
            border: '1px dashed white',
            borderRadius: '4px',
            marginTop: '12px'
          }}
        >
             
        </div>
      )}
      </div>
    </div>
  );
};

export default CategoryBlockComponent;